import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Base } from '../../../model/base';
import { Inspecao } from './../../../model/objetc/inspecao';
import { Filtro } from '../../../model/objetc/filtro/filtro';
import { Pesquisa } from '../../../model/objetc/filtro/pesquisa';
import { InspecaoService } from './../../../service/sigobra/inspecao.service';
import { TipoinspecaoService } from './../../../service/sigobra/tipoinspecao.service';
import { StatusitensinspecaoService } from './../../../service/sigobra/statusitensinspecao.service';
import { InspecaoItemTipoInspecao } from 'src/app/model/objetc/inspecaoitemtipoinspecao';
import { TipoInspecao } from 'src/app/model/objetc/tipoinspecao';
import { Obras } from 'src/app/model/objetc/obras';
import { StatusItemInspecao } from 'src/app/model/objetc/statusiteminspecao';
import { ObraService } from './../../../service/sigobra/obra.service';

@Component({
  selector: 'app-tarefas',
  templateUrl: './tarefas.page.html',
  styleUrls: ['./tarefas.page.scss'],
})
export class TarefasPage implements OnInit {

  inspecaoitens: Array<InspecaoItemTipoInspecao>;
  obras: Array<Obras>;
  statusitens: Array<StatusItemInspecao>;
  tipoinspecoes: Array<TipoinspecaoService>;
  filtro = false;
  value: string = '';

  filtros = { local: ''};
  statusPesquisa: StatusItemInspecao;
  obraPesquisa: Obras;
  tipoInspecaoPesquisa: TipoInspecao;


  constructor(private base: Base, private is: InspecaoService, private tis: TipoinspecaoService, private siis: StatusitensinspecaoService,
              private os: ObraService, private router: Router) { }

  ngOnInit() {
  
    this.carregarinspecaoitens(" ");
    this.carregarobras();
    this.carregartipoinsp();
    this.carregarstatus();
  }

  pesquisar() {
    this.carregarinspecaoitens(this.value);
  }

  realizar(inspecaoitem: InspecaoItemTipoInspecao) { 
    this.is.inspecao = inspecaoitem.inspecao;
    this.router.navigate(['inspecao-realizar']);   
  }

  carregarinspecaoitens(value: string) {
      
      this.is.pesqitem(value).subscribe(
        data => {
          this.base.dismiss();
          this.inspecaoitens = data as Array<InspecaoItemTipoInspecao>;
        }, error => {
          this.base.dismiss();
          this.base.mensagemErro('Falha ao cosultar  Inspecao Itens' + this.base.tratarErro(error));
        }
      );      
  }

  carregarstatus() {
    this.siis.pesquisar(" ").subscribe(
      data => {
        this.statusitens = data as   Array<StatusItemInspecao>;
      }, error => {
      }
    );
  }
  
  carregartipoinsp() {
    this.tis.pesquisar(" ").subscribe(
      data => {
        this.tipoinspecoes = data as  Array<TipoinspecaoService>;
      }, error => {
      }
    );
  }

  carregarobras() {
    this.os.pesquisar(" ").subscribe(
      data => {
        this.obras = data as Array<Obras>;
      }, error => {
      }
    );
  }

  habilitarFiltro() {
    this.filtro = !this.filtro;
  }

  filtar() {
    const filtro: Filtro = new Filtro();
    filtro.pesquisas = new Array<Pesquisa>();

    let pesquisa: Pesquisa;
    if (this.filtros.local !== undefined && this.filtros.local !== null && this.filtros.local !== '') {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '=';
      pesquisa.nome = 'local';
      pesquisa.valor = this.filtros.local;
      filtro.pesquisas.push(pesquisa);
    }
    if (this.obraPesquisa !== undefined && this.obraPesquisa !== null ) { 
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '=';
      pesquisa.nome = 'id';
      pesquisa.valor = this.obraPesquisa.id.toString();
      filtro.pesquisas.push(pesquisa);
    }
    if (this.tipoInspecaoPesquisa !== undefined && this.tipoInspecaoPesquisa !== null ) { 
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '=';
      pesquisa.nome = 'id';
      pesquisa.valor = this.tipoInspecaoPesquisa.id.toString();
      filtro.pesquisas.push(pesquisa);
    }
    if (this.statusPesquisa !== undefined && this.statusPesquisa !== null ) {
      
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '=';
      pesquisa.nome = 'id';
      pesquisa.valor = this.statusPesquisa.id.toString();
      filtro.pesquisas.push(pesquisa);
    }
    this.base.present();

    this.is.filtra(filtro).subscribe(
      data => {
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao filtrar registros ' + this.base.tratarErro(error));
      }
    );
  }

}
